@import "config";
@import "tags";

* {
  box-sizing: border-box;
}

body {
  @include background;
  background: $primary-color;
  color: set-text-color($primary-color);
  height: 100%;
  margin: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.5;
}

// Headings
h1,
h2,
h3 {
  margin: 0;
  font-weight: 400;

  &.lg-heading {
    font-size: 6rem;
  }

  &.sm-heading {
    margin-bottom: 2rem;
    padding: 0.2rem 1rem;
    background: rgba(lighten($primary-color, 2), 0.5);
  }
}

a {
  color: #fff;
  text-decoration: none;
  transition: all 320ms ease;
}

.break {
  display: none;
}

.text-secondary {
  color: $secondary-color;
}

//Header
.header {
  height: 100vh;
  padding: 1rem;
  &-logo {
    margin-top: 3rem;
    .portrait {
      margin: 0 auto;
      width: 250px;
      height: 250px;
      background: url('../img/portrait.png');
      background-position: center;
      background-repeat: no-repeat;
      background-size: 200px;
      border-radius: 50%;
      border: solid 3px $secondary-color;
    }    
  }

  .icons {
    margin-top: 1rem;
    a {
      padding: 0.4rem;

      &:hover {
        color: $secondary-color;
        @include easeOut();
      }
    }
  }
  .projects-header {
    position: relative;
    z-index: 3;
    .sm-heading {
      padding: 1rem;
      color: #000;
      background: none;
      text-align: center;
      
    }
  }
}

//Main
.main {
  background-color: #fff;
  padding: 4rem;
  //height: 100%;
  min-height: calc(100vh - 60px); //Item above footer - footer height!
}

//Projects
.section {
  &-heading {
    color: #000;
    font-size: 1.5rem;
    z-index: 3;
    position: relative;
    top: -8.5rem;
    text-align: center;
    cursor: pointer;
  }
}
.projects-heading {
  height: 0;
  position: absolute;
  width: 0;
  border-left: 25vw solid transparent;
  border-right: 25vw solid transparent;
  border-bottom: 100px solid #fff;
  z-index: 2;
  margin-top: -100px;
  left: 50%;
  transform: translate(-50%, -50%);
}
.projects {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 0.7rem;

  img {
    width: 100%;
    border: 3px #fff solid;

    &:hover {
      opacity: 0.5;
      border-color: $secondary-color;
      @include easeOut();
    }
  }
}

//Knowledge
.knowledge {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 1rem;

  &_button {
    font-size: 2rem;
    border: 3px #fff solid;
    padding: 1.5rem 2.5rem;
    margin-bottom: 3rem;
    cursor: pointer;
    @include easeOut;

    &:hover {
      background: $secondary-color;
      color: set-text-color($secondary-color);
      span {
        color: set-text-color($secondary-color);
      }
    }
  }
}

//Buttons
.btn {
  display: block;
  padding: 0.5rem 1rem;
  border: 0;
  margin-bottom: 0.3rem;

  &:hover {
    background: $secondary-color;
    color: set-text-color($secondary-color);
  }
}

.btn-dark {
  @extend .btn;
  background: darken($primary-color, 50);
  color: #fff;
}
.btn-light {
  @extend .btn;
  background: lighten($primary-color, 50);
  color: #333;
}
//Footer

#main-footer {
  text-align: center;
  padding: 1rem;
  background: darken($primary-color, 10);
  color: set-text-color($primary-color);
  height: 60px;
}

//Media querries
@import "mobile";